@import url(https://fonts.googleapis.com/css2?family=Baloo+2:wght@800&family=Open+Sans:wght@300;400&display=swap);
h4 {
  font-family: "Open Sans", sans-serif;
  color: #3e3e3e;
  font-size: 20px;
  font-weight: lighter;
  line-height: 2;
}

button {
  background-color: #fcca49;
  border: none;
  color: #fff;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
  outline: none;
  border-radius: 10px;
  margin-top: 15px;
}

.container {
  background-color: #f1f0f0;
}

.center-container {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  height: 100vh;
}

.home-title {
  font-family: "Baloo 2", cursive;
  color: #3e3e3e;
  font-size: 150px;
  line-height: 1;
}

.home-subtitle {
  font-family: "Open Sans", sans-serif;
  color: #3e3e3e;
  font-size: 25px;
  font-weight: 400;
  text-align: center;
}

body {
  background-color: #f1f0f0;
  height: 100vh;
}

a {
  text-decoration: none;
}

.choice-test-title-home {
  font-family: "Baloo 2", cursive;
  color: #3e3e3e;
  font-size: 15px;
  line-height: 1;
  padding: 50px 200px 0;
}

.choice-test-title {
  font-family: "Baloo 2", cursive;
  color: #3e3e3e;
  font-size: 70px;
  line-height: 1;
  text-align: center;
  padding-top: 130px;
}

.choice-test-gallery {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  width: 80%;
  max-width: 1300px;
  margin: 90px auto 0;
}

.choice-test {
  display: flex;
}

.choice-test-culture {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.choice-test-culture-web {
  font-family: "Baloo 2", cursive;
  color: #3e3e3e;
  font-size: 60px;
}

::selection {
  color: #fff;
  background: #fcca49;
}

.question-title {
  font-family: "Baloo 2", cursive;
  color: #3e3e3e;
  font-size: 50px;
  line-height: 50px;
  text-align: center;
  max-width: 1000px;
  margin: auto;
}

.question-subtitle {
  font-family: "Baloo 2", cursive;
  font-weight: 300;
  color: #3e3e3e;
  font-size: 30px;
  max-width: 1000px;
}

.question-title-home {
  font-family: "Baloo 2", cursive;
  color: #3e3e3e;
  font-size: 15px;
  line-height: 1;
  padding: 50px 200px 0;
}

.question-paragraph {
  font-family: "Open Sans", sans-serif;
  text-align: justify;
  max-width: 800px;
  font-weight: 100;
}

.question-center-content {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 70vh;
}

.question-center-content-msg {
  font-family: "Baloo 2", cursive;
  font-size: 20px;
  color: #3e3e3e;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 70vh;
}

.question-size-image {
  height: 127px;
  padding: 0px 200px;
}

.question-center-header {
  display: flex;
  justify-content: space-between;
}

.question-button {
  width: 43%;
  padding: 13px;
}

.question-content-button {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin: 40px auto 0;
  width: 52%;
  max-width: 1300px;
}

* {
  margin: 0;
  padding: 0;
}

